
body{
  background-color: rgb(58 58 85);
  margin:0;
  padding: 0;
  box-sizing: border-box;
}

.header{
    background-color: #615477;
    display: flex;
    width: 100%;
    height: 11vh;
    position: fixed;
    z-index: 999;
    top: 0rem;
    border: 3px solid black;
}

.left_view{
  flex: 80%;
  font-size: 1.5rem;
  font-weight: 600;
  padding: 1rem 1.5rem;
}

.right_view{
  flex : 20%;
  align-content: center;
  font-size: 2.2rem;
  font-style: italic;
  font-weight: 700;
}
.sec{
  width: 100vw;
  height: 100vh;
  z-index: 999;
  position: fixed;
  background-color:rgb(58 58 85);
  margin-top: 30rem;
}

.add_sub{
  margin: 10rem 20rem;
  padding-top: 2rem;
  border: 3px solid black;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  border-radius: 50px;
}

.sub_details{
    z-index: 999;
    position: fixed;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 5.5rem;
    width: 100%;
    height: 78px;
    background-color: rgb(58 58 85);
}

.Details{
  display: block;
  padding: 8px 8px;
  font-size: 1.3rem;
  font-weight: 700;
  font-style: italic;
 color:rgba(0, 0, 0, 0.8);
  background-color: rgb(58 58 85);
  cursor: pointer;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  border-bottom-right-radius: 15px;
  border-bottom-left-radius: 15px;
  border: 2px solid black;
}



.Sub{
  display: block;
  font-size: 22px;
  font-style: italic;
  color: aliceblue;
}



.details input{
  height: 20px;
  padding: 15px 20px;
  margin-top: 10px;
  border-radius: 10px;
  font-size: 20px;
  font-style: italic;
  opacity:0.2 ;
  border: none;
}

.details_add{
  display: flex;
  justify-content: center;
  align-items: center;
}

.add{
  width: 6rem;
  height: 3rem;
  margin: 3rem 10px 20px 10px;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  border-bottom-right-radius: 15px;
  border-bottom-left-radius: 15px;
  border:2px solid black;
  font-weight: 600;
  background-color: palevioletred;
  font-size: 22px;
  font-style: italic;
  cursor: pointer;
  transition: .2s ease-in-out;
}

.add:hover{
  background-color:rgb(232, 165, 187) ;
}

.add:active{
  background-color:rgb(241, 75, 131) ;
}

.List{
  position: relative;
  background-color: rgb(58 58 85);
  width:100%;
  top: 12rem;
}

.List:nth-child(1){
  display: none;
  background-color: rgb(58 58 85);
}

.attend{
  border: 2px solid black;
  box-shadow: 0 0 10px 5px rgba(0, 0, 0, 0.28);
  border-radius: 50px;
  margin: 2rem 6rem;
  padding: 1rem;
}

.subname{
  /* height:25px; */
  font-size: 30px;
  font-weight: 700;
  display: block;
  font-style: italic;
  padding: 1rem 2rem;
}

.daylist{
  font-size: 25px;
  margin: 20px 20px;
  font-style: italic;
  /* padding: 20px 30px; */
  font-weight: 700;
}

.status{
  font-size: 35px;
  font-weight: 700;
  font-style: italic;
  display: block;
  padding: 20px 30px;
  color:rgba(242, 25, 25, 0.642);
}

.count{
  display: flex;
  flex-direction: row;
  align-items: center;
}

.count div{
  margin-left: 2rem;
}

.present{
  padding: 10px 10px;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
    border-bottom-left-radius: 10px;
    font-size: 15px;
    font-style: italic;
    font-weight: 700;
    margin-top : 15px;
    cursor: pointer;
    background-color: #615477;
    border:2px solid black;
    transition:all .3s ease-in-out
}

.absent{
  padding: 10px 10px;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
  border-bottom-left-radius: 10px;
  font-size: 15px;
  font-style: italic;
  font-weight: 700;
  margin-top : 15px;
  cursor: pointer;
  background-color: #615477;
  outline: none;
  border:2px solid black;
  transition:all .2s ease-in-out
}

.delete{
  padding: 10px 10px;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
    border-bottom-left-radius: 10px;
    font-size: 15px;
    font-style: italic;
    font-weight: 700;
    margin-top : 15px;
    cursor: pointer;
    background-color: #615477;
    border:2px solid black;
}

.present:active{
  background-color: #b28df2;
}
.absent:active{
  background-color: #b28df2;
}
.delete:active{
  background-color: #b28df2;
}


@media (max-width:1000px){
  .sub_details{
    margin-top: 3rem;
  }
  .add_sub{
    margin: 10rem 10rem;
  }
}


@media   (max-width:600px) {
  .header{
    height: 15vh;
  }
  .left_view{
    flex: 60% ;
    font-size: 1.5rem;
    font-weight: 600;
    padding: 0rem;
  }
  .left_view div{
    margin: 1rem ;
  }
 .sub_details{
  margin-top: 0.8rem;
 }
  section{
    top: 100px;
  }

  .add_sub{
    padding: 2rem 1rem 2rem 1rem;
    margin-left: 1rem;
    margin-right: 1rem;
  }

  .sec{
    height: 200%;
    align-content: center;
  }
  .sec button{
    width: 50%;
  }
  
  .Sub{
    font-size: 1.5rem;
    text-align: center;
  }
.details{
  width:100%
}
  .details input{
    border-radius: 10px;
    font-size: 1.4rem;
    margin-left: 2rem;
    width: 70%;
    outline: none;
  }
  .details_add {
    width: 60%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .List{
    top: 15rem;
  }
  .attend{
    height: 24rem;
    margin:2rem
  }
  .subname{
    text-align: center;
    padding:0;
  }
  .daylist{
    padding: 1px 2rem;
    text-align: center;
    display: block;
  }
 
  .count{
    justify-content: space-around;
  }
}
